import {iosVhFix} from './utils/ios-vh-fix';
import {initModals} from './modules/modals/init-modals';
import {Form} from './modules/form-validate/form';

import {initAccordions} from './modules/liga-accordions/init-accordion';
import {initAboutButton} from './modules/liga-accordions/accordion-button';
import {initAccordionTabindex} from './modules/liga-accordions/accordion-focus';

import {checkFirstSwiper, checkSecondSwiper, thirdSecondSwiper} from './vendor.js';

import {scrollToProducts} from './modules/scroll-to';

import {openAccordion} from './modules/accordion';

// ---------------------------------

window.addEventListener('DOMContentLoaded', () => {

  // Utils
  // ---------------------------------

  iosVhFix();

  // Modules
  // ---------------------------------

  scrollToProducts();

  //       'DOMContentLoaded',     'load'
  //  load   ,      
  window.addEventListener('load', () => {
    initModals();
    const form = new Form();
    window.form = form;
    form.init();

    initAboutButton();
    initAccordions();
    initAccordionTabindex();

    checkFirstSwiper();
    checkSecondSwiper();
    thirdSecondSwiper();

    openAccordion();
  });
});

