// Swiper 7.4.1
import './vendor/swiper';

import './vendor/focus-visible-polyfill';


const firstSwiper = document.querySelector('.swiper1');
const secondSwiper = document.querySelector('.swiper2');
const thirdSwiper = document.querySelector('.swiper3');

/* eslint-disable */

const checkFirstSwiper = () => {
  if (firstSwiper) {

    const swiper = new Swiper(firstSwiper, {
      // /   
      simulateTouch: false,
      //  
      grabCursor: false,
      //    
      // slidesPerView: 4,
      //  ,     
      watchOverflow: true,
      //   
      // spaceBetween: 39,
      //   
      // slidesPerGroup: 1,
      //  
      loop: false,

      // // 
      // autoplay: {
      //   //     
      //   delay: 1000,
      //   //    
      //   stopOnLastSlide: true,
      //   //    
      //   disableOnInteraction: false,
      // },

      //   -   300
      speed: 600,

      // Navigation arrows
      navigation: {
        nextEl: '.swiper-button-next1',
        clickable: true,
      },
    });
  }
};


const checkSecondSwiper = () => {
  if (secondSwiper) {

    const swiper = new Swiper(secondSwiper, {
      simulateTouch: false,
      grabCursor: false,
      watchOverflow: true,
      loop: true,
      speed: 600,

      navigation: {
        nextEl: '.swiper-button-next2',
        clickable: true,
      },
    });
  }
};


const thirdSecondSwiper = () => {
  if (thirdSwiper) {

    const swiper = new Swiper(thirdSwiper, {
      simulateTouch: false,
      grabCursor: false,
      watchOverflow: false,
      loop: true,
      speed: 600,

      navigation: {
        nextEl: '.swiper-button-next3',
        clickable: true,
      },
    });
  }
};


